const modal = document.getElementById('successModal');
const closeModalBtn = document.getElementById('closeModalBtn');
const checkoutBtn = document.querySelector('.card_checkout_btn');

checkoutBtn.addEventListener('click', () => {
  localStorage.removeItem('cart'); // –û—á–∏—Å—Ç–∏—Ç–∏ –∫–æ—à–∏–∫
  modal.classList.remove('hidden'); // –ü–æ–∫–∞–∑–∞—Ç–∏ –º–æ–¥–∞–ª—å–Ω–µ –≤—ñ–∫–Ω–æ
});

closeModalBtn.addEventListener('click', () => {
  modal.classList.add('hidden');
  window.location.href = 'index.html'; // –ü–æ–≤–µ—Ä–Ω—É—Ç–∏—Å—å –Ω–∞ –≥–æ–ª–æ–≤–Ω—É
});
